



















export default {
	name: 'hj-dragabledrawer',
	props: {
		/**
		 * 显示状态
		 */
		options: Object
	},
	data() {
		return {
			def: {
				visible: false,
				rightMode: true,
				mask: true,
				width: 60
			},





			animationData: {},

			show: true
		}
	},
	computed: {
		option: function() {
			return { ...this.def,
				...this.options
			}
		},
		showmask:function () {
			return this.option.mask&&this.show
		}
	},
	created() {











		this.animation = uni.createAnimation();

		this.screenWidth = uni.getSystemInfoSync().windowWidth;





	},
	methods: {
		close() {
			this.options.visible = false;





		},
		updateStart: function(e) {
			this.start = e.touches[0].pageX;
			this.scale = this.screenWidth / (this.screenWidth - this.start)
		},
		updateMove: function(e) {

			this.stopref()

			this.tx = this.scale * (e.touches[0].pageX - this.start);
			this.show = false;

			if (this.option.rightMode) {
				if (this.tx > 0) {
					this.animation.translateX(this.tx).step({
						duration: 100
					})
				}
			} else {
				if (this.tx < 0) {
					this.animation.translateX(this.tx).step({
						duration: 100
					})
				}
			}
			this.animationData = this.animation.export()













		},




























		updateEnd: function() {
			this.show = true;

			this.stopref();


			if (this.option.rightMode) {
				if (this.tx > 100) {
					this.close()
				}
			} else {
				if (this.tx < -80) {
					this.close()
				}
			}
			this.animation.translateX(0).step();
			this.animationData = this.animation.export();
















		},
		stopref: uni.stopPullDownRefresh
	}
}
